import uniIcon from '@/components/uni-icon/uni-icon.vue';

export default {
    components: {
        uniIcon
    },
    props: {
        value: {
            type: Number,
            default: 0
        },
        index: {
            type: [Number, Array],
            default: 0
        },
        max: {
            type: Number,
            default: Infinity
        },
		min: {
			type: null,
			default: null
		},
        step: {
            type: Number,
            default: 1
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            inputValue: this.value
        };
    },
    computed: {
        disableAdd() {
            return this.value >= this.max;
        }
    },
    watch: {
        value(val) {
            this.inputValue = val;
        },
        inputValue(val) {
            this.$emit('change', val, this.index);
			this.$emit('input', val)
        }
    },
    methods: {
        _click() {
            this.$emit('click', this.index);
        },
        _calcValue(type) {
            const scale = this._getDecimalScale();
            let value = this.inputValue * scale;
            let step = this.step * scale;

            if (type === 'subtract') {
				if (this.min !== null && this.min === value) return;
                value -= step;
            } else if (type === 'add') {
                value += step;
            }
            if (value > this.max) {
                return;
            }
            this.inputValue = value / scale;
        },
        _getDecimalScale() {
            let scale = 1;
            // 浮点型
            if (~~this.step !== this.step) {
                scale = Math.pow(10, (this.step + '').split('.')[1].length);
            }
            return scale;
        },
        _onBlur(event) {
            let value = event.detail.value;
            if (!value) {
                this.inputValue = 0;
                return;
            }
            value = +value;
            if (value > this.max) {
                value = this.max;
            } else if (value < 0) {
                value = 0;
            }
            this.inputValue = value;
        }
    }
};
